// main page elements
body {
	font-size: 14px;
	// color: $dark
	font-family: 'ALS Schlange sans';
	font-style: normal;
	font-weight: normal;
	background: white;
	overflow-x: hidden;
 }	// +scrollbar(10px, auto, $yellow, $gray2, $dark, null)

.page-container {
	max-width: 1440px;
	width: 100%;
	margin: 0 auto;
	overflow: hidden; }

.container {
	position: relative;
	max-width: 1270px;
	width: 100%;
	padding: 0;
	margin: 0 auto;
	@include mq($max-width: 1200px) {
		padding: 0 15px;
		max-width: 100%; }
	@include mq($max-width: 575px) {
		padding: 0 10px; } }
div {
	z-index: 1; }

.hidden {
	display: none; }

.main-title {
	color: $text;
	font-weight: bold;
	font-size: 64px;
	line-height: 110%; }
.dop-title {
	font-weight: bold;
	font-size: 48px;
	line-height: 110%;
	color: $text; }
.text {
	color: $dark-grey;
	font-size: 18px;
	line-height: 150%; }
.btn {
	display: flex;
	align-items: center;
	justify-content: center;
	color: #FFFFFF;
	font-weight: bold;
	font-size: 18px;
	line-height: 150%;
	background: #A755ED;
	box-shadow: inset 5px 5px 5px rgba(255, 255, 255, 0.2), inset -5px -5px 5px rgba(0, 0, 0, 0.1);
	border-radius: 16px;
	width: 180px;
	height: 56px; }
.flex {
	display: flex; }

//start main-slider
.main-slider {
	display: flex;
	position: relative;
	height: 710px;
	margin-left: 285px;
	.main-slider__text {
		z-index: 2;
		padding-top: 143px;

		z-index: 2;
		max-width: 470px;
		h1 {
			margin-bottom: 30px; }
		p {
			margin-bottom: 30px; }
		a {} } }
.slider-bg {
	z-index: -1;
	width: 855px;
	height: 710px;
	position: absolute;
	right: 0;
	top: 0;
	background: linear-gradient(226.79deg, #C1D5D9 0%, #EFE8DB 100%);
	border-radius: 24px 0px 0px 24px; }
.mySwiper-1 {
	position: absolute;
	right: 0;
	top: 0;
	max-width: 855px;
	width: 100%;
	overflow: hidden;
	height: 710px;
	.swiper-wrapper {
		display: flex;
		align-items: center;
		padding-left: 75px;
		.swiper-slide {
			gap: 0px;
			position: relative;
			background: linear-gradient(226.79deg, #C1D5D9 0%, #EFE8DB 100%);
			border-radius: 24px;
			max-width: 330px;
			height: 460px;
			display: flex;
			align-items: center;
			justify-content: center;
			padding: 0px;
			min-width: auto;
			img {
				position: absolute; }
			span {
				position: absolute;
				right: 30px;
				top: 30px;
				font-weight: bold;
				color: white;
				font-size: 20px;
				line-height: 110%; }
			h4 {
				font-weight: bold;
				font-size: 20px;
				line-height: 110%;
				color: #9097D0;
				position: absolute;
				left: 30px;
				bottom: 30px; } }
		.swiper-slide-prev {
			width: 270px;
			height: 336px;
			-webkit-transition: all 0.3s ease;
			-moz-transition: all 0.3s ease;
			-o-transition: all 0.3s ease;
			-ms-transition: all 0.3s ease;
			transition: all 0.3s ease;
			span {
				right: 20px;
				top: 20px; }
			h4 {
				left: 20px;
				bottom: 20px; }
			img {
				max-height: 235px;
				max-width: 410px;
				width: 100%; } }
		.swiper-slide-next {
			width: 270px;
			height: 336px;
			-webkit-transition: all 0.3s ease;
			-moz-transition: all 0.3s ease;
			-o-transition: all 0.3s ease;
			-ms-transition: all 0.3s ease;
			transition: all 0.3s ease;
			img {
				max-height: 235px;
				max-width: 410px;
				width: 100%; } } } }
.mySwiper-1 {
	z-index: 2; }
//end main-slider
//start doing-reviews
