		margin-bottom: 30px; } }
.flex-we {
	position: relative;
	display: flex;
	justify-content: space-between;
	max-width: 1270px;
	margin: 5px auto; }
.box-wrapper {
	position: relative;
	z-index: 11;
	width: calc(33.3333% - 5px); }
.box-wrapper.box-wrapper--circle-right:after {
	content: '';
	position: absolute;
	top: 50%;
	right: -29.5px;
	transform: translateY(-50%);
	display: block;
	width: 42px;
	height: 42px;
	border-radius: 50%;
	background: url('data:image/svg+xml;base64,') center no-repeat;
	background-size: cover;
	z-index: 999; }
.box-wrapper.box-wrapper--circle-left:before {
	content: '';
	position: absolute;
	top: 50%;
	left: -22px;
	transform: translateY(-50%);
	display: block;
	width: 42px;
	height: 42px;
	border-radius: 50%;
	background: url('data:image/svg+xml;base64,') center no-repeat;
	background-size: cover;
	z-index: 999; }
.box {
	height: 118px;
	background-color: #fff;
	-webkit-mask-repeat: no-repeat;
	-webkit-mask-size: cover;
	padding: 20px;
	display: flex;
	align-items: center;
	font-weight: bold;
	font-size: 20px;
	line-height: 110%;
	color: $text;
	svg {
		min-width: 36px;
		min-height: 36px;
		margin-right: 23px; } }
.box-dop {
	p {
		margin-bottom: 5px; }
	a {
		font-size: 18px;
		line-height: 150%;
		color: $dark-grey;
		border-bottom: 1px dashed #37474F; } }
.box.box--left {
	position: relative;
	border-radius: 30px 0 0 30px;
	-webkit-mask-position: 100% center;
	padding-right: 40px;
	-webkit-mask-image: url('data:image/svg+xml;base64,');
	z-index: 20; }
.box.box--center {
	position: relative;
	-webkit-mask-position: 50% center, 0% center;
	-webkit-mask-size: 50% 100%;
	padding-left: 40px;
	background-color: transparent;
	z-index: 19; }
.box.box--center:after {
	content: '';
	position: absolute;
	top: 0;
	right: 0;
	width: calc(100% - 30px);
	height: 100%;
	display: block;
	background-color: #ffffff;
	-webkit-mask-size: cover;
	-webkit-mask-position: 100% center;
	-webkit-mask-repeat: no-repeat;
	-webkit-mask-image: url('data:image/svg+xml;base64,');
	z-index: -1; }
.box.box--center:before {
	content: '';
	position: absolute;
	top: 0;
	left: 0%;
	width: calc(100% - 30px);
	height: 100%;
	display: block;
	background-color: #ffffff;
	-webkit-mask-size: cover;
	-webkit-mask-position: 0% center;
	-webkit-mask-repeat: no-repeat;
	-webkit-mask-image: url('data:image/svg+xml;base64,');
	z-index: -1; }
.box.box--right {
	position: relative;
	border-radius: 0 30px 30px 0;
	-webkit-mask-position: 0% center;
	padding-left: 40px;
	-webkit-mask-image: url('data:image/svg+xml;base64,');
	z-index: 20; }
.box.box--right_2 {
	position: relative;
	border-radius: 30px;
	-webkit-mask-position: 0% center;
	padding-left: 40px;
	-webkit-mask-image: url('data:image/svg+xml;base64,');
	z-index: 20; }
.box.box--left_2 {
	position: relative;
	border-radius: 30px 0 0 30px;
	-webkit-mask-position: 100% center;
	padding-right: 40px;
	-webkit-mask-image: url('data:image/svg+xml;base64,');
	z-index: 20; }
.flex-we__one {
	.box-wrapper--circle-left:after {
		position: absolute;
		left: 50%;
		content: '';
		transform: translateX(-50%);
		display: block;
		width: 42px;
		height: 42px;
		border-radius: 50%;
		background: url(data:image/svg+xml;base64,) center no-repeat;
		background-size: cover;
		z-index: 999;
		bottom: -21px; } }
.flex-we__two {
	.box-wrapper--circle-right:before {
		position: absolute;
		left: 49.65%;
		content: '';
		transform: translateX(-49.65%);
		display: block;
		width: 42px;
		height: 42px;
		border-radius: 50%;
		background: url(data:image/svg+xml;base64,) center no-repeat;
		background-size: cover;
		z-index: 999;
		bottom: -21px; } }
.box.box--left_3 {
	position: relative;
	border-radius: 30px 0 0 30px;
	-webkit-mask-position: 100% center;
	padding-right: 40px;
	-webkit-mask-image: url('data:image/svg+xml;base64,');
	z-index: 20; }
.box.box--right_4 {
	position: relative;
	border-radius: 30px;
	-webkit-mask-position: 0% center;
	padding-left: 40px;
	-webkit-mask-image: url('data:image/svg+xml;base64,');
	z-index: 20; }
.box.box--right_5 {
	position: relative;
	border-radius: 30px;
	-webkit-mask-position: 0% center;
	padding-left: 40px;
	-webkit-mask-image: url('data:image/svg+xml;base64,');
	z-index: 20; }
.box.box--right_3 {
	position: relative;
	border-radius: 30px;
	-webkit-mask-position: 0% center;
	padding-left: 40px;
	-webkit-mask-image: url('data:image/svg+xml;base64,');
	z-index: 20; }
.box.box--center_3 {
	position: relative;
	border-radius: 30px;
	-webkit-mask-position: 0% center;
	padding-left: 40px;
	-webkit-mask-image: url('data:image/svg+xml;base64,');
	z-index: 20; }
.flex-we__four {
	gap: 10px;
	justify-content: flex-end;
	.box-wrapper--circle-top:after {
		position: absolute;
		left: 50.16%;
		content: '';
		transform: translateX(-50.16%);
		display: block;
		width: 42px;
		height: 42px;
		border-radius: 50%;
		background: url(data:image/svg+xml;base64,) center no-repeat;
		background-size: cover;
		z-index: 999;
		top: -25.7px; } }
.flex-we__one, .flex-we__two, .flex-we__three {
	.box-wrapper.box-wrapper--circle-right:after {
		right: -27.5px; } }
//end we-work
//start form
form {
	display: flex;
	justify-content: center;
	gap: 70px; }
.form {
	padding-top: 140px;
	padding-bottom: 140px;
	h2 {
		text-align: center;
		margin-bottom: 30px; }
	p {
		max-width: 693px;
		width: 100%;
		text-align: center;
		margin: 0 auto;
		margin-bottom: 60px; }
	.inputs-left {
		display: flex;
		flex-direction: column;
		gap: 30px; } }
.textarea_block {
	max-width: 430px;
	width: 100%;
	position: relative;
	height: 287px; }
.textarea_block textarea {
	display: block;
	padding: 0;
	width: 430px;
	height: 287px;
	border: none;
	border: 1px solid #B0BEC5;
	border-radius: 10px;
	background: none;
	font-size: 24px;
	resize: none;
	outline: none;
	margin: 0px;
	font-size: 18px;
	line-height: 150%;
	color: $text;
	padding-left: 20px;
	padding-right: 20px;
	padding-bottom: 15px;
	padding-top: 15px; }
.textarea_block label {
	display: block;
	width: 430px;
	height: 287px;
	position: absolute;
	top: 0;
	left: 20px;
	cursor: text; }
.textarea_block label span {
	display: block;
	height: 24px;
	-webkit-transition: all 0.3s ease;
	-moz-transition: all 0.3s ease;
	-o-transition: all 0.3s ease;
	-ms-transition: all 0.3s ease;
	transition: all 0.3s ease;
	font-size: 18px;
	line-height: 150%;
	color: $text;
	position: absolute;
	top: 15px;
	left: 0;
	cursor: text; }
.textarea_block textarea:focus+label span {
	font-weight: bold;
	font-size: 14px;
	line-height: 150%;
	color: $primary;
	top: 2px; }
.textarea_block+label span {
	font-weight: bold;
	font-size: 14px;
	line-height: 150%;
	color: $primary;
	top: 2px; }
.input_block {
	max-width: 430px;
	height: 57px;
	position: relative; }
.input_block input[type="text"] {
	font-size: 18px;
	line-height: 150%;
	color: $text;
	padding-left: 20px; }
.input_block input {
	display: block;
	padding: 0;
	width: 430px;
	height: 57px;
	border: none;
	border: 1px solid #B0BEC5;
	border-radius: 10px;
	background: none;
	font-size: 24px;
	color: #000;
	outline: none; }
.input_block label {
	display: block;
	width: 430px;
	height: 57px;
	position: absolute;
	top: 0;
	left: 20px;
	cursor: text; }
.input_block label span {
	display: block;
	height: 24px;
	-webkit-transition: all 0.3s ease;
	-moz-transition: all 0.3s ease;
	-o-transition: all 0.3s ease;
	-ms-transition: all 0.3s ease;
	transition: all 0.3s ease;
	font-size: 18px;
	line-height: 150%;
	color: $text;
	position: absolute;
	top: 15px;
	left: 0;
	cursor: text; }
.input_block input:focus+label span {
	font-weight: bold;
	font-size: 14px;
	line-height: 150%;
	color: $primary;
	top: 2px; }
.has_value+label span {
	font-weight: bold;
	font-size: 14px;
	line-height: 150%;
	color: $primary;
	top: 2px; }
//end form
