//start main-seo-two
.main-seo-two {
	padding-left: 200px;
	padding-top: 120px;
	margin-bottom: 140px;
	display: flex;
	justify-content: space-between;
	.main-seo-two__item {
		h1 {
			margin-top: 162px;
			margin-bottom: 30px; }
		.link-main {
			font-size: 18px;
			line-height: 150%;
			margin-bottom: 30px;
			color: $primary; }
		.main-seo-two__item-row {
			display: flex;
			.main-seo-two__item-cart {
				display: flex;
				align-items: center;
				svg {
					margin-right: 8px; }
				.main-seo-two__item-text {
					margin-bottom: 10px;
					width: 251px;
					.sub-title {
						font-size: 14px;
						margin-bottom: 5px;
						color: $gray;
						line-height: 150%; }
					.title {
						font-weight: bold;
						color: $text;
						font-size: 20px;
						line-height: 110%; } } } }
		position: relative;
		img {
			position: relative; }
		.button {
			position: absolute;
			left: 10px;
			top: 10px;
			display: flex;
			align-items: center;
			justify-content: center;
			background: #FFFFFF;
			box-shadow: 0px 20px 40px rgba(0, 0, 0, 0.15);
			border-radius: 15px;
			width: 60px;
			height: 60px; } } }
//end main-seo-two
//start text-block
.text-block {
	.text-block__enter {
		margin-bottom: 140px;
		h2 {
			margin-bottom: 30px; } }
	.text-block__plan > .container {

		position: absolute;
		top: 100px;
		left: 85px;
		h2 {
			margin-bottom: 30px; }
		ul {
			max-width: 960px;
			width: 100%;
			li {
				padding-left: 40px;
				margin-bottom: 30px;
				font-size: 18px;
				line-height: 150%;
				color: $dark-grey;
				position: relative; }
			li:before {
				left: 0px;
				position: absolute;
				content: '';
				width: 20px;
				height: 20px;
				border: 3px solid $primary;
				border-radius: 100%; } } }

	.text-block__plan {
		margin-bottom: 140px;
		background: linear-gradient(226.79deg, #C1D5D9 0%, #EFE8DB 100%);
		position: relative;
		max-width: 1155px;
		width: 100%;
		height: 688px;
		border-radius: 24px 0px 0px 24px; } }
.text-block__Introduction {
	margin-bottom: 60px;
	h2 {
		margin-bottom: 30px; }
	.flex {
		flex-direction: column;
		gap: 60px;
		img {
			margin: 0 auto; } } }
//end text-block
//start customer-testimonial
.customer-testimonial {
	margin-bottom: 140px;
	.container {
		h2 {
			margin-bottom: 30px; }
		.flex {
			gap: 100px;
			align-items: center;
			img {
				max-width: 670px;
				width: 100%; }
			h3 {
				font-weight: bold;
				font-size: 36px;
				line-height: 110%;
				color: $text;
				margin-bottom: 20px; }
			.text {
				max-width: 396px; } } } }
//end customer-testimonial
//start .what-came
.what-came {
	margin-bottom: 140px;
	h2 {
		margin-bottom: 30px; }
	.what-came__interested {
		display: flex;
		justify-content: space-between;
		align-items: center;
		padding: 40px;
		box-shadow: 0px 30px 80px rgba(0, 0, 0, 0.15);
		border-radius: 15px;
		h3 {
			font-weight: bold;
			font-size: 36px;
			line-height: 110%;
			color: $text;
			margin-bottom: 10px; } }
	.what-came__row {
		margin-bottom: 60px;
		display: flex;
		gap: 35px;
		.what-came__item {
			max-width: 400px;
			width: 100%;
			height: 117px;
			box-shadow: 0px 30px 80px rgba(0, 0, 0, 0.15);
			border-radius: 10px;
			display: flex;
			align-items: center;
			padding: 20px;
			svg {
				margin-right: 22px; }
			.what-came__item-text {
				h3 {
					margin-bottom: 10px;
					font-weight: bold;
					font-size: 36px;
					line-height: 110%;
					color: $text; } } } } }
//end .what-came
//start .text-page
.text-page > .container {
	padding-bottom: 140px;
	padding-left: 100px;
	padding-top: 282px;
	display: flex;
	flex-direction: column;
	gap: 60px;
	.text-page__media {
		display: flex;
		gap: 37px;
		align-items: center;
		p {
			font-size: 18px;
			line-height: 150%;
			color: $dark-grey; }
		.flex {
			gap: 20px; } }
	.text-page__testimodals {
		background: #F5F7F8;
		border-radius: 20px;
		padding: 40px;
		svg {
			max-width: 60px;
			margin-right: 70px; }
		.text-page__testimodals-text {
			h3 {
				font-weight: bold;
				color: $text;
				font-size: 18px;
				line-height: 150%;
				margin-bottom: 20px; }
			p {
				font-weight: bold;
				font-size: 16px;
				color: $gray;
				line-height: 150%; } } }
	.text-page__list-number {
		ul {
			display: flex;
			flex-direction: column;
			gap: 20px;
			li {
				font-size: 18px;
				line-height: 150%;
				color: $dark-grey;
				padding-left: 30px;
				list-style: auto;
				margin-left: 60px; } } }
	.text-page__list-square {
		ul {
			display: flex;
			flex-direction: column;
			gap: 20px;
			li {
				font-size: 18px;
				line-height: 150%;
				color: $dark-grey;
				position: relative;
				padding-left: 90px; }
			li:before {
				content: '';
				position: absolute;
				border: 3px solid #A755ED;
				width: 16px;
				height: 16px;
				left: 40px; } } }
	.text-page__images {
		display: flex;
		gap: 30px; }
	.text-page__img-one, .text-page__img-two, .text-page__img-three {
		max-width: 600px;
		width: 100%;
		height: 578px;
		margin: 0 auto; }
	.text-page__table {
		table {
			border-collapse: collapse;
			width: 100%;
			thead {
				tr {
					background: #F9FBFC;
					height: 60px;
					td {
						font-weight: bold;
						font-size: 14px;
						line-height: 150%;
						color: $gray;
						padding-left: 10px; }
					td:first-child {
						padding-left: 20px; } } }

			tbody {
				tr {
					height: 60px;
					td {
						padding-left: 10px; }
					td:first-child {
						padding-left: 20px; } }
				tr:nth-child(2n) {
					background: #F9FBFC; }
				tr:nth-child(odd) {
					background: #F5F7F8; } } } }

	.text-page__text {
		h1 {
			margin-bottom: 30px; } } }
//end .text-page
//start .contact
.contact {
	padding-top: 120px;
	padding-left: 185px;
	padding-bottom: 80px;
	display: flex;
	justify-content: space-between;
	.contact__text {
		margin-top: 162px;
		h1 {
			margin-bottom: 30px; }
		.bold {
			font-weight: bold;
			color: $dark-grey;
			font-size: 20px;
			line-height: 110%;
			margin-bottom: 20px; }
		.phone {
			margin-bottom: 30px;
			display: flex;
			align-items: center;
			img {
				margin-right: 13px; }
			.phone__text {
				font-weight: bold;
				color: $dark-grey;
				font-size: 20px;
				line-height: 110%; } }

		.text, .mail {
			display: flex;
			align-items: center;
			margin-bottom: 30px;
			font-size: 18px;
			line-height: 150%;
			color: $dark-grey;
			svg {
				margin-right: 13px; } }
		.contact__media {
			margin-bottom: 30px;
			display: flex;
			gap: 20px; } }
	.contact__map {
		position: relative;
		svg {
			position: absolute;
			z-index: 10;
			top: 0;
			left: 0;
			right: 0;
			bottom: 0;
			display: flex;
			margin: auto auto; } } }
//end .contact
.input__file {
	opacity: 0;
	visibility: hidden;
	position: absolute; }
.input__file-button {
	display: flex;
	align-items: center;
	gap: 10px;
	span {
		font-size: 18px;
		line-height: 150%;
		color: $dark-grey; } }
.input_block {
	.error {
		display: none; }
	.empty_field {
		display: block;
		border: 1px solid #E12B57; }
	span.empty_field {
		margin-top: 5px;
		border: none;
		text-align: right;
		color: #E12B57; } }
.customer-testimonial__video {
	position: relative;
	a {
		position: absolute;
		width: 60px;
		height: 60px;
		border-radius: 100%;
		background: #263238;
		opacity: 0.8;
		display: flex;
		align-items: center;
		justify-content: center;
		margin: auto;
		top: 0;
		bottom: 0;
		left: 0;
		right: 0; } }
.white-popup, .white-popup_1, , .white-popup_2 {
	position: relative;
	max-width: 1070px;
	height: 530px;
	margin: 0 auto;
	background: white;
	border-radius: 10px;
	padding-bottom: 50px;
	padding-top: 50px;
	padding-left: 70px;
	padding-right: 70px;
	iframe {
		width: 100%;
		height: 100%; }
	.form {
		padding-bottom: 0px;
		padding-top: 0px;
		p {
			margin-bottom: 30px; } } }
